@import '../../variables.scss';

.wrapper {
  position: relative;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 30px;  

  & .icon {
    border: none;
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0,0,0,.275);
    outline: none;
    width: 50px;
    height: 50px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
}

// BURGER
.menu_trigger,
.menu_trigger span {
  display: inline-block;
  transition: all .4s;
  box-sizing: border-box;
}
.menu_trigger {
  position: relative;
  width: 25px;
  height: 25px;

  & span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $mainColor;
    border-radius: 0;
  }

  & span:nth-of-type(1) {
    top: 0;
  }
  & span:nth-of-type(2) {
    top: 12px;
  }
  & span:nth-of-type(3) {
    bottom: 0;
  }

  &.active span:nth-of-type(1) {
    transform: translateY(11.5px) rotate(-45deg);
  }
  &.active span:nth-of-type(2) {
    opacity: 0;
  }
  &.active span:nth-of-type(3) {
    transform: translateY(-11.5px) rotate(45deg);
  }
}

// OVERLAY
.overlay {
  position: fixed;
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden;

  &.open {
    opacity: .9;
    visibility: visible;
    height: 100%;
    z-index: -1;

    & li {
      animation: fadeInRight .5s ease forwards;
      animation-delay: .35s;
    }
    & li:nth-of-type(2) {
      animation-delay: .4s;
    }
    & li:nth-of-type(3) {
      animation-delay: .45s;
    }
    & li:nth-of-type(4) {
      animation-delay: .50s;
    }
    & li:nth-of-type(5) {
      animation-delay: .55s;
    }
    & li:nth-of-type(6) {
      animation-delay: .60s;
    }
  }

  & nav {
    position: relative;
    height: 70%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
  }
  & ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    height: 100%;
  }
  & ul li {
    display: block;
    height: 15%;
    height: calc(100% / 4);
    max-height: 60px;
    position: relative;
    opacity: 0;
  }
  & ul li a {
    display: block;
    position: relative;
    color: $mainColor;
    text-decoration: none;
    overflow: hidden;
  }
  & ul li a:hover:after, & ul li a:focus:after, & ul li a:active:after {
    width: 100%;
  }
  & ul li a:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0%;
    transform: translateX(-50%);
    height: 3px;
    background: #FFF;
    transition: .35s;
  }
}

// MENU ITEMS ANIMATION
@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
