// OVERLAY
.overlay {
  position: fixed;
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden;

  &.open {
    opacity: .9;
    visibility: visible;
    height: 100%;
    z-index: -1;
  }

  & nav {
    position: relative;
    height: 70%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;

    .search input{
      background: transparent;
      color: $mainColor;
      border: none;
      border-bottom: 4px solid $mainColor;
      font-size: 20px;
      padding: 10px 0;
      width: 450px;
      /*line-height: 40px;
      width: 300px*/
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      // display: none;
    }
  }
}

.searchButton {
  width: 55px;
  height: 100%;
  border: none;
  text-align: center;
  color: #696b6c;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.3s;

  &:hover {
    color: $mainColor;
    transition: all 0.3s;
  }
}
