@import '../variables.scss';

.news {
  background-color: $secondaryColor;
  height: 100vh;
  padding-top: 200px;
}

.header_title {
  // margin-top: 200px;
  text-align: center;
  color: $mainColor;
}

.cards {
  max-width: 1600px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  padding: 0 50px 50px 50px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 110px;

  @include respond(lg) {
    grid-template-columns: repeat(2, 1fr);
  }
  @include respond(md) {
    grid-template-columns: repeat(2, 1fr);
  }
  @include respond(sm) {
    grid-template-columns: repeat(1, 1fr);
  }

  .card {
    float: left;
    cursor: pointer;
    background: #111;
    height: 400px;
    position: relative;
    box-shadow: 0 1px 2px rgba(0,0,0,.4);
    transition: all .2s ease;
    overflow: hidden;

    .card_image {
      max-height: 100%;
      transform: scale(1);
      transform-origin: 0 0;
      transition: .2s all ease;

      @include respond(sm) {
        transform: scale(1.21);
      }
    }

    &:hover,
    &.hover {
      box-shadow: 0 5px 15px rgba(0,0,0,.4);
      .card_image {
        transform: scale(1.2);
        opacity: .6;

        @include respond(sm) {
          transform: scale(1.3);
        }
      }
      .card_meta {
        height: 30%;
      }
      .card_details {
        top: 30%;
        
        p {
          opacity: 1;
        }
      }
    }

    .card_meta {
      position: absolute;
      height: 60%;
      top: 0;
      left: 0;
      right: 0;
      transition: .2s all ease;
    
      .date {
        background: $mainColor;
        color:#fff;
        text-transform: uppercase;
        display: inline-block;
        position: absolute;
        font-weight: bold;
        top: 20px;
        right: 20px;
        width: 40px;
        height: 40px;
        padding: 8px;
        border-radius: 50%;
        text-align: center;
        
        span {
          vertical-align: -5px;
          line-height: .8;
        }
        em {
          display: block;
          font-style: normal;
          font-size: 70%;
          padding-top: 2px;
        }
      }
    }

    .card_details {
      background: #fff;
      position: absolute;
      left: 0;
      right: 0;
      top: 70%;
      bottom: 0;
      padding: 20px;
      transition: .2s all ease;
      
      h2 {
        margin: 0;
        padding: 0 0 10px;
        color: #333333;
        font-size: $h2FontSize;
        font-weight: 700;
      }
      h3 {
        margin: 0;
        padding: 0 0 20px;
        color: $mainColor;
        font-size: $h3FontSize;
        font-weight: 400;
      }
      p {
        color: #666666;
        font-size: $fontSize;
        line-height: 1.8em;
        opacity: 0;
        transition: .4s all ease;
      }
    }
  }
}