@import '../../../variables.scss';

// BOTTOM NAV
.nav_items {
    display: flex;
  
    & ul {
      padding-right: 40px;
    
      & li {
          display: inline-block;
          padding-left: 25px;
          padding-right: 25px;
          text-align: center;
          
          a {
              text-decoration: none;
              color: $fontColor;
              position: relative;
          }
    
          a:hover {
              color: #9d1011;
              transition: .2s;
          }
      }
    }
  }
  
  //ACTIVE CLASS ON MENU ITEMS
  .active {
    &:after {
        position: absolute;
        margin: 0 auto 0 -20%;
        margin-top: 13px;
        top: 100%;
        left: 0;
        width: 140%;
        height: 4px;
        background: linear-gradient(90deg, #bd4c4d 0%, #9d1011 100%);
        content: '';
    }
  }