@import '../../variables.scss';

.wrapper{
  @include respond(lg) {
    position: absolute;
    right: 0;
    width: 100px;
    height: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @include respond(md) {
    position: relative;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    width: auto;
  }

  .call {
      background: linear-gradient(90deg, #bd4c4d 0%, #9d1011 100%);
      color: #fff;
      width: 200px;
      height: 110px;
      display: flex;
      position: absolute;
      z-index: 200;
      right: 0;
      font-family: 'Montserrat', sans-serif;
      font-weight: 300;
      font-size: 0.875em;
      font-style: normal;
      line-height: 1.5em;
      align-items: center;
      justify-content: center;

      @include respond(lg) {
        border: none;
        border-radius: 50%;
        box-shadow: 0 3px 6px rgba(0,0,0,.275);
        outline: none;
        width: 60px;
        height: 60px;
        position: relative;
      }

      @include respond(md) {
        width: 50px;
        height: 50px;
      }

      img {
        width: 40px;
        height: auto;

        @include respond(lg) {
          width: 30px;
          height: auto;
        }

        @include respond(md) {
          width: 25px;
          height: auto;
        }
    }

    .info {
      display: flex;
      flex-direction: column;
      margin-left: 12px;

      @include respond(lg) {
        display: none;
      }
    }
  }
}