.main {
  width: 100%;
  position: absolute;
  top: 185%;
}

.wrapper
{
  margin:0;
  padding:0;
  display:flex;
  justify-content: center;
  align-items: center;
  min-height: 20vh;

  top: 20%;
}

.container
{
  width:1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px,1fr));
  grid-gap: 20px;
  box-sizing: border-box;
  padding:20px;
  top: 130%;
}

.container .box
{
  position: relative;
  padding:80px 40px 40px;
  transition:0.5s;
  text-align: center;
  background:#fff;
  border-radius:10px;
  box-shadow: 0 1px 2px rgba(0,0,0,.4);
  
}

.container .box:hover
{
  background-color: #9E1112;
  box-shadow: 0 5px 15px rgba(0,0,0,.4);
}

.container .box p
{
  margin:0;
  padding:0;
  transition:0.5s;
  color: #696b6c;
}

.container .box h4
{
  margin:20px 0 0;
  padding:0;
  transition:0.5s;
  font-size:18px;
  color: #9E1112;
  font-weight:700;
  line-height: 20px;
  text-transform: uppercase;
}

.container .box h4 span
{
  font-weight: 600;
  font-size: 14px;
  color: #c5c5c5;
  transition: 0.5s;
}

.container .box:hover p,
.container .box:hover h4,
.container .box:hover h4 span
{
  color:#fff;
}

.container .box .imgBox
{
  position:absolute;
  top: -60px;
  left: 50%;
  transform: translate(-50%);
  width: 120px;
  height: 120px;
  border-radius:50%;
  overflow: hidden;

}

@media screen
{
  .container .box
  {
    margin-top: 80px;
  }
}

.contactBook {
  background-color: #fff;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100px;

  text-align: center;
  background-color: #F9F9F9;
}

.contactPhone {
  margin: 20px 0;
  font-size: 1rem;
  color: #696b6c;
  
  width: 50%;
  margin: 0 auto;
}

h2 {
  color: #9E1112;
  font-size: 2rem;
}


