@import '../../../variables.scss';

.top_inner_nav {
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid #ddd;
    height: 100%;

    // LANGUAGE SELECTION
    .lang {
      margin:0;
      display:flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      text-decoration:none;
      transition: .3s;
      border-left: 1px solid #ddd;
      border-right: 1px solid #ddd;
      padding: 0 40px;
      font-size: .8rem;
      width: 200px;
      color: #ccc;

      span {
        text-align: center;
        display: inline-block;
        width: 50px;
      }

      .active {
        color: #696b6c;
      }
    }

    // SOCIAL  
    ul {
        margin:0;
        display:flex;
        padding-right: 40px;
        align-items: center;
        justify-content: center;
      }

    ul li {
      list-style: none;
      padding-left: 5px;
      padding-right: 5px;
    }
    
    ul li a {
      position: relative;
      width:40px;
      height:40px;
      display:block;
      text-align:center;
      margin:0;
      border-radius: 50%;
      padding: 6px;
      box-sizing: border-box;
      text-decoration:none;
      transition: .3s;
    }

    ul li a:hover {
      transition: all .3s;
    } 

    ul li a:active {
      box-shadow: none;
      box-shadow: inset 0 1px 3px rgba(0,0,0,0.12), inset 0 1px 2px rgba(0,0,0,0.24);
    }
    
    ul li a i {
      width: 100%;
      height:100%;
      display:block;
      
      border-radius: 50%;
      line-height: calc(40px - 12px);
      font-size:16px;
      color: #B1B3B4;
      transition: .3s;
    }

    ul li:nth-child(1) a:hover i {
      color: #3b5998;
    }
    
    ul li:nth-child(2) a:hover i {
      color: #007bb6;
    }
    
    ul li:nth-child(3) a:hover i {
      color: #e4405f;
    }
}