$mainColor: #9d1011;
$gradient: #bd4c4d 0%, #9d1011 100%;
$secondaryColor: #F9F9F9;
$tertiaryColor: #EEEEEE;
$fontColor: #696b6c;


.wrapper {
    margin-top: 110px;
  position: relative;
}

.wrapper{
    h1 {
        color: #9d1011;
    }
    h2, h3, h4 {
  color: #696b6c;
}
}

.wrapper{
span, p {
  font-size: 1em;
  line-height: 1.61803399;
}
}

.header_img {
  position: fixed;
  left: -325px;
  top: -75px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  -webkit-filter: FlipH;
          filter: FlipH;
  -ms-filter: "FlipH";
  z-index: -1;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  padding: 100px 0 20px 0;
}
.title h1 {
  font-size: 4em;
  text-transform: uppercase;
}
.title h2 {
  font-size: 2em;
  text-transform: uppercase;
  color: #888;
  padding-bottom: 5px;
  border-bottom: 1px solid;
}
.title span {
  font-size: 0.8em;
  color: #333;
  display: inline-block;
  padding: 10px 0;
}

.prologue {
  font-style: italic;
  color: #555;
  font-size: 1.5em;
  padding-bottom: 40px;
}

.cash_outer_img {
  text-align: center;
}
.cash_outer_img .cash_img {
  width: 100%;
  max-width: 200px;
  padding: 5px;
  
}

.biography p {
  padding: 10px 0;
}
.biography p:first-of-type:first-letter {
  font-size: 3em;
  line-height: 0.9;
}
.wrapper {
button {
  padding: 15px;
  background: #111;
  color: #fff;
  margin: 10px 0 100px 0;
  border: none;
  -webkit-transition: background-color 0.15s ease-in-out;
  transition: background-color 0.15s ease-in-out;
}
button:hover {
  background: #222;
}
}

@media (max-width: 1400px) {
  .header_img {
    left: -400px;
  }
}
@media (max-width: 1200px) {
  .header_img {
    left: -500px;
  }
}
@media (max-width: 1000px) {
  .header_img {
    left: -600px;
  }
}
@media (max-width: 850px) {
  .header_img {
    display: none;
  }
}
