.App {
    background-color: #F9F9F9;
    color: #696b6c;
    text-align: center;
}

.formCard {
    

    width: 80%;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;

    padding: 20px;

    
    box-shadow: 0 -5px 20px rgba(0, 0, 0, -0.75), 0 4px 82px rgba(0, 0, 0, 0.22);
    transition: all 0.5s cubic-bezier(.25,.8,.25,1);
    overflow: hidden;
    position: relative;
    background-color: #fff;
}

.mapouter{height:500px;width:100%;margin-top: 50px;}
.gmap_canvas {overflow:hidden;background:none!important;height:500px;width:100%;}

.header_title {
    text-align: center;
    color: #9d1011;
    padding-top: 200px;
}

.contactBook {
    background-color: #fff;
    margin: 100px 0;
    padding: 60px 0;

    h2 {
        color: #696b6c;
    }
}

.contactPhone {
    margin: 20px 0;
    font-size: 1.5rem;
    color: #9E1112;
    font-weight: 800;
}
