$mainColor: #9d1011;
$gradient: #bd4c4d 0%, #9d1011 100%;
$secondaryColor: #F9F9F9;
$tertiaryColor: #EEEEEE;
$fontColor: #696b6c;
$fontSize: calc(12px + 2 * ((100vw - 320px)/ 1920));
$h2FontSize: calc(1rem + 3 * ((100vw - 320px)/ 1920));
$h3FontSize: calc(.8rem + 3 * ((100vw - 320px)/ 1920));

$breakpoints: (
  xs: 576px,
  sm: 768px,
  md: 992px,
  lg: 1200px
);

@mixin respond($breakpoint) {
    @if map-has-key($breakpoints, $breakpoint) {
      $breakpoint-value: map-get($breakpoints, $breakpoint);
      @media (max-width: ($breakpoint-value - 1)) {
        @content;
      }
    } @else {
      @warn 'Invalid breakpoint: #{$breakpoint}.';
    }
  }
